import apiFetch from '@wordpress/api-fetch';
import { Button, PanelBody, PanelRow } from '@wordpress/components';
import {
  InnerBlocks,
  InspectorControls,
  MediaUpload,
  MediaUploadCheck,
} from '@wordpress/block-editor';
import { registerBlockType } from '@wordpress/blocks';
import { useEffect } from '@wordpress/element';

registerBlockType('ourblocktheme/slide', {
  title: 'Slide',
  description: 'A slide to be inserted inside the Slideshow block',
  supports: {
    align: ['full'],
  },
  attributes: {
    align: { type: 'string', default: 'full' },
    imgID: { type: 'number' },
    imgURL: { type: 'string', default: banner.fallbackimage },
    themeimage: { type: 'string' },
  },
  edit: EditComponent,
  save: SaveComponent,
});

function EditComponent(props) {
  useEffect(function () {
    if (props.attributes.themeimage != '') {
      props.setAttributes({
        imgURL: `${slide.themeimagepath}${props.attributes.themeimage}`,
      });
    }
  }, []); //provide theme inbuilt image if the user still didn't provide another image (see functions.php and above attributes)

  useEffect(() => {
    if (props.attributes.imgID) {
      async function go() {
        const response = await apiFetch({
          path: `/wp/v2/media/${props.attributes.imgID}`,
          method: 'GET',
        });
        props.setAttributes({
          imgURL: response.media_details.sizes.pageBanner.source_url,
        });
      }
      go();
    }
  }, [props.attributes.imgID]);

  function onFileSelect(x) {
    props.setAttributes({ imgID: x.id });
  }

  return (
    <>
      {/* change BG image from the sidebar in editor */}
      <InspectorControls>
        <PanelBody title='Background' initialOpen={true}>
          <PanelRow>
            <MediaUploadCheck>
              <MediaUpload
                onSelect={onFileSelect}
                value={props.attributes.imgID}
                render={({ open }) => {
                  return <Button onClick={open}>Choose Image</Button>;
                }}
              />
            </MediaUploadCheck>
          </PanelRow>
        </PanelBody>
      </InspectorControls>
      <div className='hero-slider__slide'>
        <div className='hero-slider__interior container'>
          <div className='hero-slider__overlay t-center'>
            <InnerBlocks
              allowedBlocks={[
                'ourblocktheme/genericheading',
                'ourblocktheme/genericbutton',
              ]}
            />
          </div>
        </div>
      </div>
      <div
        className='page-banner__bg-image'
        style={{
          backgroundImage: `url('${
            props.attributes.imgURL
              ? props.attributes.imgURL
              : '/images/library-hero.jpg'
          }')`,
        }}
      ></div>
    </>
  );
}

//rendering directly from PHP (changes in the banner.php html will be directly rendered, on the fly, dynamically):
function SaveComponent() {
  return <InnerBlocks.Content />;
}
